import { Grid2 as Grid, Typography } from '@mui/material';
import { useState } from 'react';
import { motion } from 'framer-motion';
import CustomButton from './CustomButton';

import ProjectCard from './ProjectCard';
import B from '../../assets/learnr-hero.png';
import C from '../../assets/garage-hero.png';
import A from '../../assets/goodcause-hero.png';
import D from '../../assets/ntui-hero.png';
import Right from '../../assets/right-arrow-button.png';
import Left from '../../assets/left-arrow-button.png';

import Image from './Image';

const projects = [
    {
        id: "1", 
        image: A,
        title: "GoodCause",
        description: "January - May 2024,\nFor EEE DIP, UI/UX and Asset Team",
        link: "/ndp-portfolio/web-development/school-projects"
    }, 
    {
        id: "2", 
        image: B,
        title: "Learnr.sg",
        description: "May - October 2024,\nSoftware Developer Intern and Part-Time",
        link: "/ndp-portfolio/web-development/learnr"
    }, 
    {
        id: "3", 
        image: C,
        title: "Garage@EEE Events",
        description: "August 2024 - Present,\nBranding and Marketing Member",
        link: "/ndp-portfolio/graphic-design/garage"
    }, 
    {
        id: "4", 
        image: D,
        title: "NTU Fencing Invites",
        description: "February - July 2023/24,\nPublicity Committee Member",
        link: "/ndp-portfolio/graphic-design/invites"
    }
]; 

const ProjSlideshow = () => {
    const [activeIndex, setActiveIndex] = useState(1);
    const [disabled, setDisabled] = useState(false);

    const handleNext = () => {
        setDisabled(true); 
        setActiveIndex((prev) => (prev + 1) % projects.length);
        setTimeout(() => setDisabled(false), 600);
    }
      const handlePrev = () => {
        setDisabled(true); 
        setActiveIndex((prev) => (prev - 1 + projects.length) % projects.length);
        setTimeout(() => setDisabled(false), 600);
    }

    const activeProject = projects[activeIndex];

    return (
        <Grid container sx={{
            display: "flex", 
            flexDirection: "column", 
            justifyContent: "center", 
            alignItems: "center",
            gap: "2em",
            }}>
            <Grid 
                sx={{
                    height: "40vh",
                    display: "flex", 
                    justifyContent: "center", 
                    alignItems: "center", 
                    position: "relative", 
                    width: "auto", 
                    marginTop: {lg: "-2.5em", md: 0, xs: "-25em"},
                    marginBottom: {md: 0, xs: "-6em"},
                    scale: {xl: 1, md: 0.70, sm: 0.45, xs: 0.35}
                }}
            >
                {projects.map((src, index) => {
                    let position = "hidden"; 

                    if (index === activeIndex) {
                    position = "active"; 
                    } else if (index === (activeIndex - 1 + projects.length) % projects.length) {
                    position = "left"; 
                    } else if (index === (activeIndex + 1) % projects.length) {
                    position = "right"; 
                    }

                    return (
                    <motion.div
                        key={src.id}
                        className={position}
                        initial={{ opacity: 0, scale: 0.5 }}
                        animate={{
                        opacity: position === "active" ? 1 : 0.8,
                        scale: position === "active" ? 1 : 0.8,
                        x: position === "left" ? "-27.5em" : position === "right" ? "27.5em" : 0,
                        y: position === "active" ? 0 : "3.5em",
                        rotateY: position === "left" ? -10 : position === "right" ? 10 : 0,
                        }}
                        transition={{ duration: 0.6, ease: "easeInOut" }}
                        style={{
                        position: "absolute",
                        width: "400px",
                        height: "250px",
                        zIndex: position === "active" ? 5 : position === "left" ? 1 : 0,
                        backgroundColor: "#FBFBFB",
                        }}
                    >
                        <ProjectCard key={src.id} src={src.image} />
                    </motion.div>
                    );
                })}
            </Grid>


            <Grid 
                container 
                sx = {{
                    flexDirection: "row", 
                    justifyContent: "center",
                    width: {xl: "45vw", lg: "55vw", md: "80vw", sm: "80vw", xs: "95vw"}, 
                    position: "relative"
                }}
            >
                <CustomButton onClick={handlePrev} 
                    disabled={disabled}
                    circular={true}
                    sx = {{ 
                        position: "absolute",
                        left: 0,
                        top: 20
                    }}
                >
                    <Image src={Left}/>
                </CustomButton>

                <Typography 
                    variant="h1" 
                    sx = {{
                        fontFamily: "Anton-SC, sans-serif", 
                        textAlign: "center", 
                        marginTop: "0.5em",
                        fontSize: {
                            md: "4rem",
                            xs: "2.5rem"
                        }
                    }}
                >
                    {activeProject.title}
                </Typography>

                <CustomButton onClick={handleNext} 
                disabled={disabled}
                circular={true}
                sx = {{ 
                    position: "absolute",
                    right: 0,
                    top: 20
                }}
                >
                    <Image src={Right}/>
                </CustomButton>
            </Grid>
            <Typography 
                variant="h3" 
                sx = {{ 
                    textAlign: "center",
                    fontSize: {
                        md: "2rem",
                        xs: "1.75rem"
                    }
                }}
            >
                {activeProject.description.split("\n").map((line, index) => (
                    <span key={index}>
                        {line}
                        <br />
                    </span>
                ))}
            </Typography>
            <CustomButton 
                sx = {{backgroundColor: "#88FFDB", maxWidth: "10em"}}
                href={activeProject.link} 
            >
                view more
            </CustomButton>

        </Grid>
    );
};

export default ProjSlideshow;